.new-task {
	position: fixed;
	left: 0;
	height: 100vh;
	width: 500px;
	transition: 0.5s all ease-out;
}

.inactive {
	left: -600px;
}

.form-card {
	position: relative;
	top: 12rem;
	width: 80%;
	margin: auto auto;
	padding: 20px 30px;
	background-color: white;
	border-radius: 5px;
	box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.form-title {
	text-align: center;
	margin-bottom: 10px;
}

.group {
	margin: 30px 0;
	display: flex;
	justify-content: space-around;
}

.group label {
	flex: 1;
}

.group input {
	flex: 1;
}

.button-group {
	display: flex;
	justify-content: center;
	margin-top: 40px;
}

hr {
	border-top: 1px solid rgba(0, 0, 0, 0.1);
	margin-bottom: 30px;
}

.create-task-form {
	margin-top: 30px;
}

.title-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 5px;
}
