.input {
	border: none;
	border-bottom: 1px solid rgba(0, 0, 0, 0.2);
	font-size: 18px;
	width: 100%;
	margin: 10px;
	box-sizing: border-box;

	padding-bottom: 8px;
	padding-left: 8px;
	margin: 10px 0;
	transition: 0.2s all ease-in;
}

.input:active,
.input:focus {
	outline: none;
	border-bottom: 1px solid blue;
}
