.card {
	padding: 30px;
	background-color: white;
	border-radius: 10px;
	margin: 20px;
	box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
		rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}

.modal-card {
	box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
		rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.modal-title {
	text-align: center;
	margin-bottom: 20px;
}

.modal-buttons {
	margin-top: 20px;
}
