.header-container {
	position: fixed;
	top: 0;
	left: 0;
	background-color: #5300c7;
	color: white;
	height: 4rem;
	width: 100%;
	box-sizing: border-box;
}

.header {
	box-sizing: border-box;
	display: flex;
	height: 4rem;
	justify-content: space-between;
	padding: 5px 20px;
	align-items: center;
}

.header-controls {
	display: flex;
	justify-content: space-between;
}
