.task-list-holder {
	position: relative;
	top: 4rem;
	left: 0;
	z-index: -3;
	width: 50%;
	margin: 20px auto;
	transition: 0.5s all ease-out;
}

.no-list {
	text-align: center;
	color: rgba(0, 0, 0, 0.8);
}

.task-list-title {
	text-align: center;
	padding-bottom: 10px;
	color: rgba(0, 0, 0, 0.7);
	border-bottom: 1px solid rgba(0, 0, 0, 0.1);
	margin-bottom: 20px;
}

.form-active {
	left: 200px;
}
