.button {
	margin: 5px 20px;
	padding: 10px 40px;
	color: white;
	background-color: #6200ea;
	border: none;
	border-radius: 4px;
	font-size: 16px;
	cursor: pointer;
	transition: 0.1s all ease-in;
}

.button:active,
.button:focus {
	outline: none;
}

.button:hover {
	background-color: #5300c7;
}

.close {
	background-color: #f1f4f8;
	color: #202020;
}

.close:hover {
	background-color: #d9e2ef;
}

.header-button {
	background-color: #f1f4f8;

	color: black;
}

.header-button:hover {
	background-color: #d6dbe2;
}

.button:disabled {
	cursor: not-allowed;
	background-color: gray;
}
